	import mpvueCityPicker from 'mpvue-citypicker';
	import mpvuePicker from '../../components/ly-picker.vue';
	import http from '../../utils/http.js';
	import api from '../../utils/api.js';
	export default {
		data() {
			return {
				 imageSrc: '',
				 address:'请选择地址',//常住地
				 types:'请选择商家类型',
				 pickerValueDefault: [0, 0, 0],
				 index:0,
				 mode: 'multiLinkageSelector',
				 deepLength: 2,
				 pickerValueArray: [
					{
					  label: '火车',
					  value: 10,
					  children: [
						{
						  label: '火车1',
						  value: 11,
						  children: [
							
						  ]
						},
						{
						  label: '火车22',
						  value: 212,
						  children: []
						},
						{
						  label: '火车2',
						  value: 12,
						  children: []
						},
						{
						  label: '火车3',
						  value: 13,
						  children: []
						}
					  ]
					},
					{
					  label: '火车20',
					  value: 210,
					  children: [
						{
						  label: '火车21',
						  value: 211,
						  children: [
							
						  ]
						},
						{
						  label: '火车22',
						  value: 212,
						  children: []
						},
						{
						  label: '火车23',
						  value: 213,
						  children: []
						}
					  ]
					}
				  ],
				  addressProvince:'',//省
				  addressCity:'',//市,
				  addressCounty:'',//区
				  merchantCategoryFirst:'',//yiji
				  merchantCategorySecond:'',//二级
				  addressDetail:'',//详细地址
				  imageUrl:'',//照片
				  leadingName:'',//负责人名称
				  name:'',//商户名称
				  phone:'',//联系方式
			}
		},
		components: {
			mpvueCityPicker,
			mpvuePicker
		},
// 		onUnload() {
// 
// 		},
		onShow(){
			this.queryCategory()
		},
		onLoad() {
			this.imageSrc = '';
			console.log(this.pickerValueArray)
			
		},
		methods: {
			// 获取商户类型
			queryCategory(){
				let self=this
				let param={
					
				}
				http.Request(api.queryCategory,'POST',param,function(res){
					console.log(res)
					self.pickerValueArray=res
					console.log(self.pickerValueArray)
				})
			},
			// 上传图片
			chooseImage: function() {
				uni.chooseImage({
					count: 1,
					sizeType: ['compressed'],
					sourceType: ['camera'], //album
					success: (res) => {
						console.log('chooseImage success, temp path is', res.tempFilePaths[0])
						var imageSrc = res.tempFilePaths[0]
						uni.uploadFile({
							url: 'https://api30.chuanhuoapp.com/business-user/v1/yt/uploadImage',
							filePath: imageSrc,
							fileType: 'image',
							name: 'file',
							success: (res) => {
								console.log( JSON.parse(res.data))
								let datas=JSON.parse(res.data)
								uni.showToast({
									title: '上传成功',
									icon: 'success',
									duration: 1000
								})
								this.imageSrc = datas.data
							},
							fail: (err) => {
								console.log('uploadImage fail', err);
								uni.showModal({
									content: err.errMsg,
									showCancel: false
								});
							}
						});
					},
					fail: (err) => {
						console.log('chooseImage fail', err)
					}
				})
			},
			// 删除正面
			imageSrcOneNone(){
				this.imageSrc=''
			},
			// 商家类型
			 showPicker() {
				 console.log(this.$refs.mpvuePicker)
			  this.$refs.mpvuePicker.show();
			},
			// 选择省市区
			showCityPicker() {
				console.log(this.$refs.mpvueCityPicker)
			  this.$refs.mpvueCityPicker.show();
			},
			onChange(e) {
			  console.log(e);
			},
			onChangepicker(e) {
			  console.log(e);
			},
			onCancel(e) {
				// 取消事件
			  console.log(e);
			  this.$refs.mpvueCityPicker.hide();
			},
			onCancelpicker(e) {
				// 取消事件
			  console.log(e);
			  this.$refs.mpvuePicker.hide();
			}, 
			// 确定事件
			onConfirm(e) {
			  console.log(e);
			  this.address=e.label;
			  let newaddress=this.address.split("-")
			  console.log(newaddress)
			  this.addressProvince=newaddress[0];
			  this.addressCity=newaddress[1];
			  this.addressCounty=newaddress[2];
			  this.$refs.mpvueCityPicker.hide();
			},
			// 确定事件
			onConfirmpicker(e) {
			  console.log(e);
			  this.types=e.label
			  let newtypes=this.types.split("-")
			  console.log(newtypes)
			  this.merchantCategoryFirst=newtypes[0];
			  this.merchantCategorySecond=newtypes[1];
			  this.$refs.mpvuePicker.hide();
			},
			// 点击加入
			join(){
				if(this.address=='请选择地址'){
					uni.showToast({
						icon:'none',
						title: '请选择地区'
					});
					return
				}
				if(this.name==''){
					uni.showToast({
						icon:'none',
						title: '请填写商家名称'
					});
					return
				}
				if(this.phone==''){
					uni.showToast({
						icon:'none',
						title: '请填写商家手机号'
					});
					return
				}
				if(!(/^1[3456789]\d{9}$/.test(this.phone))){
				       uni.showToast({
				       	icon:'none',
				       	title: '请填写正确的手机号码'
				       });  
				       return false; 
				} 
				if(this.types=='请选择商家类型'){
					uni.showToast({
						icon:'none',
						title: '请选择商家类型'
					});
					return
				}
				if(this.leadingName==''){
					uni.showToast({
						icon:'none',
						title: '请填写负责人名称'
					});
					return
				}
				if(this.addressDetail==''){
					uni.showToast({
						icon:'none',
						title: '请填写详细地址'
					});
					return
				}
				if(this.imageSrc==''){
					uni.showToast({
						icon:'none',
						title: '请上传门店合影照片'
					});
					return
				}
				if((/^ +| +$/g).test(this.name)){
					uni.showToast({
						icon:'none',
						title: '商家名称含有空格'
					});
					return
				}
				if((/^ +| +$/g).test(this.leadingName)){
					uni.showToast({
						icon:'none',
						title: '负责人名称含有空格'
					});
					return
				}
				if((/^ +| +$/g).test(this.phone)){
					uni.showToast({
						icon:'none',
						title: '负责人手机号含有空格'
					});
					return
				}
				if((/^ +| +$/g).test(this.addressDetail)){
					uni.showToast({
						icon:'none',
						title: '详细地址含有空格'
					});
					return
				}
				this.saveMerchant()
			},
			// 添加上户api
			saveMerchant(){
				let self=this
				let param={
					addressCity:self.addressCity,//市
					addressCounty:self.addressCounty,//区
					addressDetail:self.addressDetail,//商家地址（详细地址）
					addressProvince:self.addressProvince,//（省）
					image:self.imageSrc,//
					// image:'123',//
					leadingName:self.leadingName,//负责人姓名
					merchantCategoryFirst:self.merchantCategoryFirst,//一级类目
					merchantCategorySecond:self.merchantCategorySecond,//二级类目名
					name:self.name,//商户名称
					phone:self.phone,//商户联系方式
				}
				http.Request(api.saveMerchant,'POST',param,function(res){
					console.log(res)
					let query=res
					console.log(query)
					uni.setStorageSync('codeUrl', res);
					uni.reLaunch({
						url: '../settledStepTwo/settledStepTwo?query=1'
					});
				})
			}
		}
	}
